<template >
  <image-modal :visible="visible" @close="close" transition="roll">
    <div v-if="loading">Loading</div>
    <div v-else>
      <div v-for="(img, index) in images" class="imgWr" @click="selectImage(index)">
        <img  :src="imageUrl(index)" alt="">
      </div>
    </div>
  </image-modal>
</template>

<script>
import { ImageModal } from 'vue-bulma-modal'

export default {
  components: {
    ImageModal
  },

  created: function () {
    this.getImage(1)
    console.log(this.image)
  },
  props: {
    visible: Boolean,
    image: Object
  },

  data () {
    return {
      loading: true,
      images: []
    }
  },

  computed: {

  },

  methods: {
    close () {
      this.$emit('close')
    },
    getImage (page) {
      let baseUrl = 'http://vnguy.com'
      this.$http.get(baseUrl + '/image/get/' + page)
        .then(res => {
          this.loading = false
          this.images = res.data
        }).catch(err => {
          console.log(err)
        })
    },
    imageUrl: function (index) {
      return 'http://vnguy.com/image/' + this.images[index].path
    },
    selectImage (index) {
      console.log(this)
      this.$parent.$emit('onSelectImage', this.images[index])
    }
  }

}
</script>

<style>
  .modal-content{
    width: 80%;
  }
  .modal-content .imgWr {
    max-width: 320px;
    max-height: 100px;
    display: inline-block;
    border: 1px solid #ddd;
    cursor: pointer;
  }
</style>



